<template>
	<div id="select-content">
		<select-top-nav class="select-nav" @changeMenuClick="changeMenuClick" :list="list"/>
		<div class="pd10s">
			<select-item v-for="(item,index) in newData" :key="index" :index="index" :itemInfo="item" class="select-item">
			<div class="left-inline-box" slot="leftselect"  @click.stop="itemClick(index,item.id)">
				<i class="select-icon-p"  :class="{selectaction:currentIndex==index}" ></i>
				<span>{{item.title}}</span>
			</div>
			<div class="right-inline-box" @click="nextmenuClick(item,index)" slot="nextmenu" v-show="isShow">
				下级
			</div>
			</select-item>
		</div>
	</div>
</template>

<script>

import SelectItem from './SelectItem'
import SelectTopNav from  './SelectTopNav'

export default {
  name: 'SelectContent',
	components:{
		SelectItem,
		SelectTopNav
	},
	data(){
		return{
			//这个数组是给顶部显示导航栏的
			list:[
				{title:'顶层',id:0},
			],
			isShow:true,
		}
	},
	props:{
		newData:{
			type:Array,
			default(){
				return []
			}
		},
		menuSelect:{
			type:Object,
			default(){
				return {}
			}
		}
	},
	methods:{
		itemClick(index){
			if(this.menuSelect.currentIndex==index){
				this.menuSelect.currentIndex=-1
				this.menuSelect.title=""
			}else{
				this.menuSelect.currentIndex=index
				//这里有选中了-将选中的id和数组传进临时对象保存
				//这里改变获取的值
				this.menuSelect.title=this.newData[this.currentIndex].title
				this.menuSelect.id=this.newData[this.currentIndex].id
			}
		},
		//下级菜单点击
		nextmenuClick(item,index){
			console.log("?")
			if(this.menuSelect.currentIndex!=-1){
				alert("选中状态下不能展开")
				return false
			}
			this.getNewData(item.id)
			//将金属放进去
			this.list.push({title:'->'+this.newData[index]['title'],id:item.id})
			//设置下一级选中为空
			this.deleteClick()
			
			//这里需要判断 该选中的选项中的parentid是否为0
			if(item.parentid!==0){
				this.isShow=false
			}
		},
		//顶部菜单点击
		changeMenuClick(id,index){
			this.deleteClick()
			//this.getNewData(id)
			this.$emit("getNewData",id)
			if(index==0){
				this.isShow=true
			}
			this.list.splice(index+1,this.list.length)
		},
		//清空点击选中的
		deleteClick(){
			this.menuSelect.currentIndex=-1
			this.menuSelect.title=''
			this.menuSelect.id=0
		},
		//获取新数据
		getNewData(id){
			this.$store.dispatch("getClassifyData",{cid:id});
		}
	},
	computed:{
		currentIndex(){
			return this.menuSelect.currentIndex
		}
	},
	created(){
		//执行action里的方法
		this.getNewData(0)
	},
}
</script >

<style spoced>
#select-content{
	color:#5d5555;
	height:calc(100% - 91px);
}

.select-icon-p{
	border:1px solid #ccc;
	border-radius:50px;
	padding:1px 10px;
	margin-right:15px;
}

.right-inline-box{
	display:inline-block;
	width:19.9%;
	text-align:center;
	font-size:12px;
	color:blue;
}

.left-inline-box{
	display:inline-block;
	width:80%;
	border-right:1px solid #eaeaea;
}
.selectaction{
	background:#576cff;
	border:1px solid #576cff;
	font-color:white;
}
.pd10s{
	background:#fff;
	padding:0 10px;
}
</style>
