<template>
	<div id="select-product">
		<!--顶部菜单-->
		<nav-bar class="salesslip-nav-bar">
			<div slot="left" class="icon-left" @click="backClick">
				<img src="~assets/images/common/prev2.svg">
			</div>
			<div slot="center">
				货品类型
			</div>
			<div slot="right">
				图标
			</div>
		</nav-bar>
		<select-content  @getNewData="getNewData" :new-data="getParsenMenu" :menu-select="menuSelect"/>
		<select-tabbar/>
	</div>
</template>

<script>

import NavBar from 'components/common/navbar/NavBar'
import SelectContent from 'components/contents/select/SelectContent'
import SelectTabbar from  'components/contents/select/SelectTabbar'

export default {
  name: 'SelectProduct',
	components: {
		NavBar,
		SelectContent,
		SelectTabbar
	},
	data(){
		return{
		}
	},
	methods:{
		//返回按钮
		backClick(){
			this.$router.go(-1)
			//清空选项
			this.$store.state.menuSelect.currentIndex=-1
			this.$store.state.menuSelect.title=""
		},
		getNewData(id){
			this.$store.dispatch("getClassifyData",{cid:id});
		},
	},
	computed:{
		getParsenMenu(){
			return this.$store.getters.getParsenMenu
		},
		menuSelect(){
			return this.$store.state.menuSelect
		},
	},
}
</script >

