<template>
	<div class="select-tabbar">
		<div class="select-tag">
			<span class="tag-icon" v-show="$store.state.menuSelect.title" @click="cancleClick">{{$store.state.menuSelect.title}}</span>
		</div>
		<div class="select-success">
			<span @click="successClick">确定</span>
		</div>
	</div>
</template>

<script>


export default {
  name: 'SelectTabbar',
	components: {
	},
	methods:{
		//确定并且跳转到添加商品页面
		successClick(){
			console.log("确定")
			//将临时的变成持久的
			this.$store.state.selectClassify.title=this.$store.state.menuSelect.title
			this.$store.state.selectClassify.id=this.$store.state.menuSelect.id
			this.$router.go(-1)
		},
		//取消
		cancleClick(){
			//标签点击取消选中的类别
			this.$store.state.menuSelect.title=""
			this.$store.state.menuSelect.currentIndex=-1
		}
	}
}
</script >

